* {
  box-sizing: border-box; /* Учитывает padding и border в ширине */
}
.task-window {
    width: calc(100% - 52px); /* Учитываем отступы и границы */
    border-radius: 5px; 
    background-color: #FFFFFF;     
    padding: 20px; 
    margin: 26px;        
    word-wrap: break-word; 
    overflow-wrap: break-word; 
    white-space: normal; 
    max-height: 548px; /* Минимальная высота */
    overflow: auto; 
     scrollbar-width: thin; /* Для Mozilla */
}
.task-window::-webkit-scrollbar {
    width: 8px; /* Ширина полосы прокрутки */
    height: 8px; /* Высота полосы прокрутки */
}

.task-window::-webkit-scrollbar-thumb {
    background-color: #ccc; /* Цвет полосы прокрутки */
    border-radius: 4px; /* Радиус скругления */
}

.task-window::-webkit-scrollbar-track {
    background-color: #f0f0f0; /* Цвет фона полосы прокрутки */
}
.close-button {
    float: right; /* Выравнивание кнопки закрытия вправо */
}

.task-name {
    margin: 0; 
    font-size: 24px; /* Размер шрифта заголовка */
    color: #000000; 
    text-align: left; 
    margin-bottom: 35px;   
   word-wrap: break-word; /* Позволяет переносить длинные слова */
   overflow-wrap: break-word; /* Дополнительное свойство для переноса текста */
   white-space: normal; 
   flex: 1;
   max-width: 900px; 
}

.description-container {
    border: 1px solid #ccc; 
    padding: 10px; /* Отступы вокруг описания */
    cursor: pointer; 
    margin-bottom: 35px;
    max-width: 100%; /* Ограничение по ширине */
    width: 100%;
    background-color: #FFFFFF;    
}

.description-textarea {
    width: 60%; /* Ширина текстового поля */
}
.TaskButton__save {
    color: #5E6C84;
    cursor: pointer;
    margin-top: 15px;
    border: none; 
    padding: 4px; 
    text-align: left;
   
}
.TaskButton__save:hover {
    background-color: #e0e0e0;
    color: #3a3a3a; 
    border-radius: 5px; 
}
.close-button {
    background: none;
    border: none; 
    color: black; /* Цвет крестика */
    font-size: 18px; 
    cursor: pointer; 
}
.main-task {
    background-color: #0079BF;
    justify-content: flex-start; /* Измените на flex-start, чтобы не растягивать */
    min-height: 590px;
    width: 100%; /* Занимает всю доступную ширину */
    max-width: 1235px; /* Максимальная ширина */
    margin: 0 auto; /* Центрирование */
    flex: 1;
    display: flex;
    box-sizing: border-box; /* Учитывает отступы и границы в ширине */
    padding: 20px; /* Добавьте отступы, если нужно */
}