
body { 
  background-color: #0079BF;
  margin: 0;
  padding: 0;  
  position: relative;
}

body::before,
body::after {
    content: ""; 
    position: absolute;
    left: 0; 
    right: 0; 
    height: 55px; 
    background-color: #0067A3; 
}
body::before {
    top: 0;
}
body::after {
    bottom: 0; 
}
body, h1, h2, h3, p {
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; 
}
button, input, select {
    border: none;
    outline: none;
    background: none;
    font: inherit;
    color: inherit;
    padding: 0;
    margin: 0;
    cursor: pointer;
}

.header, .footer, .main {
  max-width: 1235px; 
  margin: 0 auto;
  width: 100%; 
  display: flex; 
 
}
h2 {
  text-align: center;
}
.header {
  background-color: #0067A3;
  color: #FFFFFF;  
  height: 55px;
  align-items: center; 
  justify-content:space-between;
  padding-left: 20px;
  padding-right: 16px;
   z-index: 1;
}
.header__title {  
  font-weight: 400; 
  font-size: 28px; 
}
.user-menu {
  display: flex; 
  align-items: center; 
  width: 64px; 
  height: 40px; 
  border-radius: 5px; 
  position: relative; 
  cursor: pointer;
}

.user-menu__logo {
  width: 30px; 
  height: 30px; 
  border-radius: 50%; 
  object-fit: cover; 
  background-color: #FFFFFF;  
}
.user-menu__arrow {
width: 24px;
height: 24px;
cursor: pointer;
}
.dropdown-menu {
  position: absolute; /* Позиционирование выпадающего меню */
  top: 44px; /* Отступ от верхней части */
  left:-80px;
  color: #000000;
  size: 14px;
  font-weight: 400;
  width:134px;
  height: 60px;
  background-color: white; /* Цвет фона */
  border: 1px solid #ccc; /* Граница */
  border-radius: 5px; /* Закругление углов */   
  z-index: 1000; /* Поверх других элементов */
}

.dropdown-item {
  padding: 5px; 
  cursor: pointer;
}

.dropdown-item:hover {
  color: #0067A3; /* Цвет фона при наведении */
  text-decoration: underline; /* Подчеркивание при наведении */
}
/* Стрелка для выпадающего меню */
.dropdown-arrow {
  position: absolute;
  top: -8px; 
  left: 71%; /* Центрирование стрелки */
  transform: translateX(-50%); 
  width: 0;
  height: 0;
  border-left: 9px solid transparent; 
  border-right: 9px solid transparent; 
  border-bottom: 9px solid white; /* Нижний треугольник, цвет фона меню */
}

.footer {
  background-color: #0067A3;
  color: #FFFFFF;
  height: 55px; 
  align-items: center; 
  justify-content:space-between;
  padding-left: 20px;
  padding-right: 16px;
   z-index: 1;
}
.task-status {
  display: flex; 
  flex-direction:row;
  gap:36px;
  font-weight: 400;
  font-size: 18px; 
}
.main{
  background-color: #0079BF;     
  justify-content:space-between;  
 flex: 1;
  
}
.kanban-board {
  display: flex;
  justify-content: space-between;
  padding: 20px; 
  
}

.kanban-board__columns {
  display: flex;
  flex: 1;
  gap: 24px;
  align-items: flex-start; /* Выравнивание по верхней части */  
}

.kanban-board__column {
  flex: 1;  
  border: 1px solid #ccc;
  border-radius: 10px;
  padding: 12px;
  width: 282px; 
  background-color: #EBECF0;
  max-height: 538px; /* Устанавливаем максимальную высоту */
  overflow-y: auto; /* Добавляет вертикальную прокрутку */
  overflow-x: hidden; /* Скрывает горизонтальную прокрутку, если не нужна */  
}

/* Стилизация полосы прокрутки для WebKit (Chrome, Safari) */
.kanban-board__column::-webkit-scrollbar {
  width: 12px; /* Ширина полосы прокрутки */
  border-radius: 0; /* Убираем скругление */
}

.kanban-board__column::-webkit-scrollbar-track {
  background: #FFFFFF; /* Цвет фона полосы прокрутки */
  border-radius: 0; /* Убираем скругление */
}

.kanban-board__column::-webkit-scrollbar-thumb {
  background-color: #CBCBCB; /* Цвет ползунка */
  border-radius: 0; /* Убираем скругление */
  height: 52px; /* Высота ползунка */
}

/* Стилизация полосы прокрутки для Firefox */
.kanban-board__column {
  scrollbar-width: thin; 
  scrollbar-color: #CBCBCB #FFFFFF; 
}

/* Дополнительные стили для Firefox (обратите внимание, что ::-moz-scrollbar не поддерживается) */
.kanban-board__column::-moz-scrollbar {
  width: 12px; 
}

.kanban-board__column::-moz-scrollbar-track {
  background: #FFFFFF; 
}

.kanban-board__column::-moz-scrollbar-thumb {
  background-color: #CBCBCB; 
  border-radius: 0; 
  height: 52px; 
}

.kanban-board__column-title {
  font-size: 18px;
  text-align: left; 
  margin-bottom: 15px;
}

.kanban-board__task-list {
  list-style-type: none;
  padding: 0;
}

.kanban-board__task-item {
  cursor: pointer;
  margin-bottom: 15px;
  padding: 5px;
  border: 1px solid #ddd;
  border-radius: 3px;
  display: flex;
  justify-content: space-between;
}

.kanban-board__add-button {
  color: #5E6C84; /* Цвет текста */
  cursor: pointer;
  background-color: transparent; /* Убираем фон */
  border: none; /* Убираем рамку */
  padding: 4px; /* Убираем отступы */
  text-align: left; 
}

.kanban-board__add-button--active:hover {
  background-color: #FFFFFF; 
  border-radius: 5px; 
}

.kanban-board__add-button--disabled { 
  cursor: not-allowed;
}

.kanban-board__task-input {
  margin-bottom: 10px;
  
}
.kanban-board__task-item {
  width: 258px; 
  min-height: 35px; 
  background-color: #FFFFFF; 
  border: none; 
  border-radius: 5px; 
  padding: 7px;
  word-wrap: break-word; /* Позволяет переносить длинные слова */
  overflow-wrap: break-word; /* Дополнительное свойство для переноса текста */
  white-space: normal; /* Позволяет тексту переноситься на новую строку */
  display: block; /* Убедитесь, что элемент блочный */
}

.kanban-board__input {
  width: 258px; 
  min-height: 35px; 
  background-color: #FFFFFF; 
  border: none; 
  border-radius: 5px; 
  padding: 7px;
}

.kanban-board__submit-button {
  margin-top: 10px;
  color: #5E6C84; 
  cursor: pointer;
  background-color: transparent; 
  border: none; 
  padding: 4px; 
  text-align: left;
  
}
.kanban-board__submit-button:hover {
  background-color: #0079BF; 
  border-radius: 5px;
  color:#FFFFFF;
}

.kanban-board__add-task-toggle {
  color: #5E6C84; 
  cursor: pointer;
  background-color: transparent; 
  border: none; 
  padding: 4px; 
  text-align: left;
}
.kanban-board__add-task-toggle:hover {
  background-color: #FFFFFF;
  border-radius: 5px; 
}
.kanban-board__no-columns {
  margin-top: 20px;
  font-style: italic;
  color: #999;
  text-align: center;
}
.kanban-board__select {
  width: 258px;
  height: 35px;
  background-color: #FFFFFF;
  border: none; 
  border-radius:5px; 
  padding: 0; 
  cursor: pointer;
}
.kanban-board__select option {
  background-color: #FFFFFF; 
  color: #000000; 
  border-radius: 5px;
  cursor: pointer;
}

.kanban-board__select option:hover {  
  color: #000000; 
  margin-top: 30px;  
  cursor: pointer;
}

.kanban-board__select:focus {
  outline: none; 
}
.wrapper {
   display: flex;
  flex-direction: column;
  min-height: 100vh;
}
@media screen and (max-width: 1224px) {
  .main{
  background-color: #0079BF; 
  display: flex;
  justify-content: flex-start;  
  min-height: 590px;
   flex-direction: column;
  
}
  .kanban-board {   
    flex-wrap: wrap; 
    padding: 20px; 
    width: 100%; 
    justify-content: center; 
  }
  
  .kanban-board__columns {    
    flex-wrap: wrap;    
    justify-content: center; 
    
  }
  
  .kanban-board__column {
    flex: 0 0 282px; 
    max-width: 282px; 
  }
  .task-name {
    max-width: 650px;
  }
}

@media screen and (max-width: 768px) {
  .task-status__name {
    display: none;
  }
  .task-status {    
    gap:15px;   
  }
  .header {
    background-color: #0079BF;   
    margin-bottom: 50px;
  }
  .header__title {    
    font-size: 16px; 
    color: #0079BF; 
  }  
  body { 
    background-color:#0079BF; 
  }
 
  .task-name {
    max-width: 400px;
  }
}
@media screen and (max-width: 568px) {
   .task-name {
    max-width: 250px;
  }
}
@media screen and (max-width: 368px) {
   .task-name {
    max-width: 170px;
  }
}